import React, { useState, useEffect } from 'react';
import { recruiterAPI } from '../services/apiService';
import '../index.css';
import '../recruiter.css';

const EditJobPage = ({ onBack, onJobUpdated, recruiterId, jobId, onDeleteJob }) => {
  // Define districts and fields options
  const districts = [
    'Northern District',
    'Haifa District', 
    'Central District',
    'Tel Aviv District',
    'Jerusalem District',
    'Southern District'
  ];


  const fields = [
    'Software Development', 'Data Science', 'Cybersecurity', 'DevOps', 'UI/UX Design',
    'Product Management', 'Marketing', 'Sales', 'Finance', 'Human Resources',
    'Operations', 'Customer Success', 'Business Development', 'Content Creation',
    'Graphic Design', 'Digital Marketing', 'Project Management', 'Quality Assurance',
    'System Administration', 'Network Engineering', 'Mobile Development', 'Web Development',
    'Game Development', 'Artificial Intelligence', 'Machine Learning', 'Cloud Computing',
    'Database Administration', 'Technical Writing', 'Research & Development', 'Consulting'
  ];

  const degreeOptions = [
    'No Degree',
    'Bachelor\'s Degree',
    'Master\'s Degree',
    'PhD or Doctorate',
    'College Diploma',
    'Professional Certification',
    'Bootcamp Graduate',
    'High School Diploma'
  ];

  const [jobData, setJobData] = useState({
    title: '',
    description: '',
    company: '',
    status: 'Active',
    qualifications: [],
    ticket: {
      location: '',
      experience: '',
      remote: 'On-Site',
      jobType: 'Full-time',
      field: '',
      degree: ''
    }
  });

  const [isLoading, setIsLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [successMessage, setSuccessMessage] = useState('');
  const [newQualification, setNewQualification] = useState('');

  // Set localStorage flags when component mounts to ensure persistence
  useEffect(() => {
    if (jobId) {
      localStorage.setItem('isOnEditJobPage', 'true');
      localStorage.setItem('editingJobId', jobId);
    }
  }, [jobId]);

  // Load job data when component mounts
  useEffect(() => {
    const loadJobData = async () => {
      if (jobId) {
        try {
          setIsLoading(true);
          // For now, we'll get the job from the recruiter's jobs list
          // In a real implementation, you'd have a specific API endpoint to get a single job
          const result = await recruiterAPI.getMyJobs(recruiterId);
          if (result.success && result.data) {
            const job = result.data.find(j => j.jobId === parseInt(jobId));
            if (job) {
              setJobData({
                title: job.title || '',
                description: job.description || '',
                company: job.company || '',
                status: job.status || 'Active',
                qualifications: job.qualifications || [],
                ticket: {
                  location: job.ticket?.location || job.location || '',
                  experience: job.ticket?.experience || job.experience || '',
                  remote: job.ticket?.remote || job.remote || 'On-Site',
                  jobType: job.ticket?.jobType || job.jobType || 'Full-time',
                  field: job.ticket?.field || job.field || ''
                }
              });
            }
          }
        } catch (error) {
          console.error('Failed to load job data:', error);
          setErrors({ general: 'Failed to load job data' });
        } finally {
          setIsLoading(false);
        }
      }
    };

    loadJobData();
  }, [jobId, recruiterId]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    if (name.startsWith('ticket.')) {
      const ticketField = name.split('.')[1];
      setJobData(prev => ({
        ...prev,
        ticket: {
          ...prev.ticket,
          [ticketField]: value
        }
      }));
    } else {
      setJobData(prev => ({
        ...prev,
        [name]: value
      }));
    }
    
    // Clear errors when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleAddQualification = () => {
    if (newQualification.trim() && !jobData.qualifications.includes(newQualification.trim())) {
      setJobData(prev => ({
        ...prev,
        qualifications: [...prev.qualifications, newQualification.trim()]
      }));
      setNewQualification('');
    }
  };

  const handleRemoveQualification = (index) => {
    setJobData(prev => ({
      ...prev,
      qualifications: prev.qualifications.filter((_, i) => i !== index)
    }));
  };

  const handleQualificationKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleAddQualification();
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!jobData.title.trim()) {
      newErrors.title = 'Job title is required';
    }
    
    if (!jobData.description.trim()) {
      newErrors.description = 'Job description is required';
    }
    
    if (!jobData.company.trim()) {
      newErrors.company = 'Company name is required';
    }
    
    if (!jobData.ticket.location) {
      newErrors['ticket.location'] = 'Location is required';
    }
    
    if (!jobData.ticket.experience) {
      newErrors['ticket.experience'] = 'Experience level is required';
    }
    
    if (!jobData.ticket.field) {
      newErrors['ticket.field'] = 'Field is required';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSave = async () => {
    if (!validateForm()) {
      return;
    }
    
    setIsLoading(true);
    setSuccessMessage('');
    
    try {
      const jobToUpdate = {
        jobId: parseInt(jobId),
        recruiterId: parseInt(recruiterId),
        title: jobData.title,
        description: jobData.description,
        company: jobData.company,
        status: jobData.status,
        ticket: jobData.ticket
      };
      
      const result = await recruiterAPI.updateJob(jobToUpdate);
      
      if (result.success) {
        setSuccessMessage('Job updated successfully!');
        // Clear localStorage when job is updated successfully
        localStorage.removeItem('isOnEditJobPage');
        localStorage.removeItem('editingJobId');
        setTimeout(() => {
          onJobUpdated();
        }, 1000); // Reduced timeout for faster navigation
      } else {
        setErrors({ general: result.message || 'Failed to update job' });
      }
    } catch (error) {
      console.error('Failed to update job:', error);
      setErrors({ general: 'Failed to update job' });
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    onBack();
  };

  const handleDelete = async () => {
    if (!window.confirm('Are you sure you want to delete this job? This action cannot be undone and will delete all related applications and offers.')) {
      return;
    }

    if (onDeleteJob && jobId) {
      try {
        await onDeleteJob(jobId);
        // The parent component will handle navigation after deletion
      } catch (error) {
        console.error('Error deleting job:', error);
      }
    }
  };

  if (isLoading && !jobData.title) {
    return (
      <div className="create-job-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading job data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="create-job-page">
      <div className="page-header">
        <button className="back-btn" onClick={handleCancel}>
          <i className="fas fa-arrow-left"></i> Go Back to Portal
        </button>
        <h1>Edit Job Posting</h1>
      </div>

      <div className="page-content">
        {successMessage && (
          <div className="success-message">
            <i className="fas fa-check-circle"></i>
            {successMessage}
          </div>
        )}

        {errors.general && (
          <div className="error-message">
            <i className="fas fa-exclamation-triangle"></i>
            {errors.general}
          </div>
        )}

        <div className="form-container">
          <div className="form-section">
            <h2>Job Information</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Job Title *</label>
                <input
                  type="text"
                  className={`form-input ${errors.title ? 'error' : ''}`}
                  name="title"
                  value={jobData.title}
                  onChange={handleInputChange}
                  placeholder="Enter job title"
                />
                {errors.title && <span className="error-message">{errors.title}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Company *</label>
                <input
                  type="text"
                  className={`form-input ${errors.company ? 'error' : ''}`}
                  name="company"
                  value={jobData.company}
                  onChange={handleInputChange}
                  placeholder="Enter company name"
                />
                {errors.company && <span className="error-message">{errors.company}</span>}
              </div>
            </div>

            <div className="form-group">
              <label className="form-label">Job Description *</label>
              <textarea
                className={`form-textarea ${errors.description ? 'error' : ''}`}
                name="description"
                value={jobData.description}
                onChange={handleInputChange}
                placeholder="Describe the role, responsibilities, and requirements..."
                rows="6"
              />
              {errors.description && <span className="error-message">{errors.description}</span>}
            </div>
          </div>

          <div className="form-section">
            <h2>Job Details</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Location *</label>
                <select
                  className={`form-select ${errors['ticket.location'] ? 'error' : ''}`}
                  name="ticket.location"
                  value={jobData.ticket.location}
                  onChange={handleInputChange}
                >
                  <option value="">Select District</option>
                  {districts.map(district => (
                    <option key={district} value={district}>{district}</option>
                  ))}
                </select>
                {errors['ticket.location'] && <span className="error-message">{errors['ticket.location']}</span>}
              </div>

              <div className="form-group">
                <label className="form-label">Experience (Years) *</label>
                <input
                  type="number"
                  className={`form-select ${errors['ticket.experience'] ? 'error' : ''}`}
                  name="ticket.experience"
                  value={jobData.ticket.experience}
                  onChange={handleInputChange}
                  min="0"
                  max="100"
                  required
                />
                {errors['ticket.experience'] && <span className="error-message">{errors['ticket.experience']}</span>}
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Remote Work</label>
                <select
                  className="form-select"
                  name="ticket.remote"
                  value={jobData.ticket.remote}
                  onChange={handleInputChange}
                >
                  <option value="On-Site">On-Site</option>
                  <option value="Remote">Remote</option>
                  <option value="Hybrid">Hybrid</option>
                </select>
              </div>

              <div className="form-group">
                <label className="form-label">Job Type</label>
                <select
                  className="form-select"
                  name="ticket.jobType"
                  value={jobData.ticket.jobType}
                  onChange={handleInputChange}
                >
                  <option value="Full-time">Full-time</option>
                  <option value="Part-time">Part-time</option>
                  <option value="Contract">Contract</option>
                  <option value="Internship">Internship</option>
                  <option value="Freelance">Freelance</option>
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Field *</label>
                <select
                  className={`form-select ${errors['ticket.field'] ? 'error' : ''}`}
                  name="ticket.field"
                  value={jobData.ticket.field}
                  onChange={handleInputChange}
                >
                  <option value="">Select Field</option>
                  {fields.map(field => (
                    <option key={field} value={field}>{field}</option>
                  ))}
                </select>
                {errors['ticket.field'] && <span className="error-message">{errors['ticket.field']}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Degree</label>
                <select
                  className="form-select"
                  name="ticket.degree"
                  value={jobData.ticket.degree}
                  onChange={handleInputChange}
                >
                  <option value="">Select Degree</option>
                  {degreeOptions.map(degree => (
                    <option key={degree} value={degree}>{degree}</option>
                  ))}
                </select>
              </div>
            </div>
          </div>

          <div className="form-section">
            <h2>Job Qualifications</h2>
            <div className="form-group">
              <label className="form-label">Required Qualifications</label>
              <div className="qualifications-input">
                <input
                  type="text"
                  className="form-input"
                  value={newQualification}
                  onChange={(e) => setNewQualification(e.target.value)}
                  onKeyPress={handleQualificationKeyPress}
                  placeholder="Enter a qualification requirement"
                />
                <button
                  type="button"
                  className="btn btn-secondary btn-sm"
                  onClick={handleAddQualification}
                  disabled={!newQualification.trim()}
                >
                  <i className="fas fa-plus"></i> Add
                </button>
              </div>
              
              {jobData.qualifications.length > 0 && (
                <div className="qualifications-list">
                  <h4>Current Qualifications:</h4>
                  <ul className="qualifications-tags">
                    {jobData.qualifications.map((qualification, index) => (
                      <li key={index} className="qualification-tag">
                        <span>{qualification}</span>
                        <button
                          type="button"
                          className="remove-qualification"
                          onClick={() => handleRemoveQualification(index)}
                        >
                          <i className="fas fa-times"></i>
                        </button>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>

          <div className="form-actions">
            <button 
              className="btn btn-secondary" 
              onClick={handleCancel}
              disabled={isLoading}
            >
              Cancel
            </button>
            <button 
              className="btn btn-danger" 
              onClick={handleDelete}
              disabled={isLoading}
            >
              <i className="fas fa-trash"></i> Delete Job
            </button>
            <button 
              className="btn btn-primary" 
              onClick={handleSave}
              disabled={isLoading}
            >
              {isLoading ? 'Updating...' : 'Update Job'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditJobPage;
